import React, { Component } from 'react';
import Header from '../Header/Header';
import gql from 'graphql-tag';
import { Query } from 'react-apollo';
import Link from 'next/link';
import Error from '../Utils/ErrorMessage';
import styled from 'styled-components';
import Head from 'next/head';
import DeleteItem from './DeleteItem';
import AddToCart from '../Cart/AddToCart';


const SingleItemStyles = styled.div`
  width: 100%;
  border: 1px solid black;

  
  .singleitem-top {
    display: flex;
    align-items:center;
    padding: 0 5rem;

    &-details {
      margin-left: 5rem;
    }

    .singleitem-top-details-buttons {
      width: 100%;
    }
  }

  img {
    width: 50%;
    height: auto;
    border: 1px solid ${props => props.theme.lightgrey};
  }

  .details {
    margin: 3rem;
    font-size: 2rem;
  }
`;

const SINGLE_ITEM_QUERY = gql`
    query SINGLE_ITEM_QUERY( $id: ID!) {
        item(where: {id: $id }) {
            id 
            title
            description
            largeImage
        }
    }
`;

export class SingleItem extends Component {
  render() {
    return (
      <>
        <Header />
        <Query
            query={SINGLE_ITEM_QUERY}
            variables={{
                id: this.props.id
            }}>
            {({ error, loading, data }) => {
                if(error) return <Error error={error} />;
                if(loading) return <p>Loading...</p>;
                // Check item length
                if (!data.item) return <p>No Item Found for {this.props.id}</p>;
                const item = data.item;
                return (
                    <SingleItemStyles>
                      <Head>
                        <title>Super Shoes | {item.title}</title>
                      </Head>
                      <div className="singleitem-top">
                        <img src={item.largeImage} alt={item.title} />
                        <div className="singleitem-top-details">
                          <h2>{item.title}</h2>
                          <div className="singleitem-top-details-buttons">
                            <Link
                            href={{
                              pathname: 'update',
                              query: { id: item.id },
                            }}
                            >
                              <a>Edit ✏️</a>
                            </Link>
                            <AddToCart id={item.id} />
                            <DeleteItem id={item.id}>Delete This Item</DeleteItem>
                          </div>
                        </div>
                      </div>
                      <div className="details">
                        <p>{item.user}</p>
                        <p>{item.description}</p>
                      </div>
                    </SingleItemStyles>
                );
            }}    
        </Query>
      </>
    )
  }
}

export default SingleItem;
